"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { createClient } from "@/lib/supabase/client";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { Switch } from "@/components/ui/switch"; // si tu n'as pas ce composant shadcn, remplace par un simple checkbox

interface Category {
  id: string;
  name: string;
}

interface VariantForm {
  id: string;
  color: string;
  size: string;
  price: string;
  quantity: string;
}

export default function NewProductPage() {
  const router = useRouter();
  const supabase = createClient();

  const [categories, setCategories] = useState<Category[]>([]);
  const [isLoading, setIsLoading] = useState(false);

  const [formData, setFormData] = useState({
    name: "",
    description: "",
    price: "",
    compareAtPrice: "",
    sku: "",
    quantity: "",
    categoryId: "",
    status: "published",
  });

  // üì∑ plusieurs images
  const [imageFiles, setImageFiles] = useState<File[]>([]);
  const [imagePreviews, setImagePreviews] = useState<string[]>([]);

  // üß© variantes
  const [hasVariants, setHasVariants] = useState(false);
  const [variants, setVariants] = useState<VariantForm[]>([]);

  useEffect(() => {
    loadCategories();
  }, []);

  const loadCategories = async () => {
    try {
      const {
        data: { user },
      } = await supabase.auth.getUser();
      if (!user) return;

      const { data: shop, error: shopError } = await supabase
        .from("shops")
        .select("id")
        .eq("user_id", user.id)
        .single();

      if (shopError || !shop) {
        console.error("Shop error:", shopError);
        return;
      }

      const { data, error } = await supabase
        .from("categories")
        .select("id, name")
        .eq("shop_id", shop.id)
        .order("name", { ascending: true });

      if (error) {
        console.error("Categories error:", error);
        return;
      }

      setCategories(data || []);
    } catch (err) {
      console.error("Unexpected categories error:", err);
    }
  };

  const handleChange = (
    e:
      | React.ChangeEvent<HTMLInputElement>
      | React.ChangeEvent<HTMLTextAreaElement>
      | React.ChangeEvent<HTMLSelectElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  // üì∑ s√©lection multiple d'images
  const handleImagesChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (!files || files.length === 0) {
      setImageFiles([]);
      setImagePreviews([]);
      return;
    }

    const arr = Array.from(files);
    setImageFiles(arr);
    setImagePreviews(arr.map((f) => URL.createObjectURL(f)));
  };

  // üß© gestion variantes
  const addVariant = () => {
    setVariants((prev) => [
      ...prev,
      {
        id: crypto.randomUUID(),
        color: "",
        size: "",
        price: formData.price || "",
        quantity: formData.quantity || "0",
      },
    ]);
  };

  const updateVariant = (
    id: string,
    field: keyof Omit<VariantForm, "id">,
    value: string
  ) => {
    setVariants((prev) =>
      prev.map((v) => (v.id === id ? { ...v, [field]: value } : v))
    );
  };

  const removeVariant = (id: string) => {
    setVariants((prev) => prev.filter((v) => v.id !== id));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      const {
        data: { user },
      } = await supabase.auth.getUser();

      if (!user) {
        alert("Utilisateur non connect√©");
        return;
      }

      const { data: shop, error: shopError } = await supabase
        .from("shops")
        .select("id")
        .eq("user_id", user.id)
        .single();

      if (shopError || !shop) {
        console.error("Shop error:", shopError);
        alert("Boutique introuvable pour cet utilisateur");
        return;
      }

      // ==================================================
      // 1) Cr√©er le produit (sans images et sans variantes pour l'instant)
      // ==================================================
      const { data: inserted, error: insertError } = await supabase
        .from("products")
        .insert({
          shop_id: shop.id,
          name: formData.name,
          description: formData.description,
          price: formData.price ? parseFloat(formData.price) : 0,
          compare_at_price: formData.compareAtPrice
            ? parseFloat(formData.compareAtPrice)
            : null,
          sku: formData.sku || null,
          quantity: hasVariants
            ? 0 // si variantes, on peut ignorer le stock global
            : formData.quantity
            ? parseInt(formData.quantity, 10)
            : 0,
          category_id: formData.categoryId || null,
          status: formData.status,
        })
        .select("id")
        .single();

      if (insertError || !inserted) {
        console.error("Insert product error:", insertError);
        alert("Erreur lors de la cr√©ation du produit : " + insertError?.message);
        return;
      }

      const productId = inserted.id as string;

      // ==================================================
      // 2) Uploader les images
      // ==================================================
      let mainImageUrl: string | null = null;

      if (imageFiles.length > 0) {
        const imageRows: { product_id: string; image_url: string; sort_order: number }[] = [];

        for (let index = 0; index < imageFiles.length; index++) {
          const file = imageFiles[index];
          const ext = file.name.split(".").pop();
          const baseName =
            typeof crypto !== "undefined" && "randomUUID" in crypto
              ? (crypto as any).randomUUID()
              : Date.now().toString();

          const fileName = `${baseName}.${ext}`;
          const filePath = `${shop.id}/${productId}/${fileName}`;

          const { data: uploadData, error: uploadError } = await supabase
            .storage
            .from("product-images")
            .upload(filePath, file, {
              cacheControl: "3600",
              upsert: true,
            });

          if (uploadError) {
            console.error("Upload image error:", uploadError);
            alert("Erreur upload image : " + uploadError.message);
            continue;
          }

          const { data: publicData } = supabase
            .storage
            .from("product-images")
            .getPublicUrl(uploadData?.path || filePath);

          const url = publicData.publicUrl;
          if (!mainImageUrl && url) {
            mainImageUrl = url;
          }

          imageRows.push({
            product_id: productId,
            image_url: url,
            sort_order: index,
          });
        }

        if (imageRows.length > 0) {
          const { error: imgInsertError } = await supabase
            .from("product_images")
            .insert(imageRows);

          if (imgInsertError) {
            console.error("Insert product_images error:", imgInsertError);
          }
        }
      }

      // ==================================================
      // 3) Cr√©er les variantes si activ√©es
      // ==================================================
      if (hasVariants && variants.length > 0) {
        const variantRows = variants
          .filter((v) => v.price) // au moins un prix
          .map((v, index) => ({
            product_id: productId,
            name: `${v.color || ""}${v.color && v.size ? " / " : ""}${
              v.size || ""
            }`.trim() || null,
            color: v.color || null,
            size: v.size || null,
            price: parseFloat(v.price || formData.price || "0"),
            quantity: v.quantity ? parseInt(v.quantity, 10) : 0,
            sort_order: index,
          }));

        if (variantRows.length > 0) {
          const { error: variantsError } = await supabase
            .from("product_variants")
            .insert(variantRows);

          if (variantsError) {
            console.error("Insert product_variants error:", variantsError);
          }
        }
      }

      // ==================================================
      // 4) Mettre √† jour l'image principale du produit
      // ==================================================
      if (mainImageUrl) {
        const { error: updateError } = await supabase
          .from("products")
          .update({ image_url: mainImageUrl })
          .eq("id", productId);

        if (updateError) {
          console.error("Update main image error:", updateError);
        }
      }

      router.push("/dashboard/products");
    } catch (err: any) {
      console.error("Error creating product:", err);
      alert(
        "Erreur inattendue lors de la cr√©ation du produit : " +
          (err?.message || "voir console")
      );
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>Ajouter un produit</CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-2">
                <Label htmlFor="name">Nom du produit</Label>
                <Input
                  id="name"
                  name="name"
                  value={formData.name}
                  onChange={handleChange}
                  required
                  disabled={isLoading}
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="price">Prix (de base)</Label>
                <Input
                  id="price"
                  name="price"
                  type="number"
                  step="0.01"
                  value={formData.price}
                  onChange={handleChange}
                  required
                  disabled={isLoading}
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="compareAtPrice">Prix barr√© (optionnel)</Label>
                <Input
                  id="compareAtPrice"
                  name="compareAtPrice"
                  type="number"
                  step="0.01"
                  value={formData.compareAtPrice}
                  onChange={handleChange}
                  disabled={isLoading}
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="quantity">Stock global</Label>
                <Input
                  id="quantity"
                  name="quantity"
                  type="number"
                  value={formData.quantity}
                  onChange={handleChange}
                  disabled={isLoading || hasVariants}
                />
                {hasVariants && (
                  <p className="text-xs text-gray-500">
                    Le stock sera g√©r√© par variante.
                  </p>
                )}
              </div>

              <div className="space-y-2">
                <Label htmlFor="sku">SKU</Label>
                <Input
                  id="sku"
                  name="sku"
                  value={formData.sku}
                  onChange={handleChange}
                  disabled={isLoading}
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="categoryId">Cat√©gorie</Label>
                <select
                  id="categoryId"
                  name="categoryId"
                  className="border rounded-md px-3 py-2 w-full"
                  value={formData.categoryId}
                  onChange={handleChange}
                  disabled={isLoading}
                >
                  <option value="">Sans cat√©gorie</option>
                  {categories.map((cat) => (
                    <option key={cat.id} value={cat.id}>
                      {cat.name}
                    </option>
                  ))}
                </select>
              </div>

              {/* üì∑ Upload multiple */}
              <div className="space-y-2 md:col-span-2">
                <Label htmlFor="images">Images du produit</Label>
                <Input
                  id="images"
                  type="file"
                  accept="image/*"
                  multiple
                  onChange={handleImagesChange}
                  disabled={isLoading}
                />
                {imagePreviews.length > 0 && (
                  <div className="mt-2 flex flex-wrap gap-2">
                    {imagePreviews.map((src, i) => (
                      <img
                        key={i}
                        src={src}
                        alt={`Preview ${i + 1}`}
                        className="h-20 w-20 object-cover rounded-md border"
                      />
                    ))}
                  </div>
                )}
              </div>
            </div>

            {/* üß© Variantes */}
            <div className="space-y-3 border-t pt-4">
              <div className="flex items-center justify-between">
                <div>
                  <p className="font-semibold">Variantes (taille / couleur)</p>
                  <p className="text-xs text-gray-500">
                    Activez si ce produit existe en plusieurs couleurs ou tailles.
                  </p>
                </div>
                <div className="flex items-center gap-2">
                  <span className="text-xs text-gray-500">D√©sactiv√©</span>
                  <Switch
                    checked={hasVariants}
                    onCheckedChange={(checked) => setHasVariants(checked)}
                    disabled={isLoading}
                  />
                  <span className="text-xs text-gray-500">Activ√©</span>
                </div>
              </div>

              {hasVariants && (
                <div className="space-y-3">
                  {variants.map((v) => (
                    <div
                      key={v.id}
                      className="grid grid-cols-1 md:grid-cols-5 gap-2 items-end border rounded-md p-2"
                    >
                      <div className="space-y-1">
                        <Label>Couleur</Label>
                        <Input
                          value={v.color}
                          onChange={(e) =>
                            updateVariant(v.id, "color", e.target.value)
                          }
                          placeholder="Rouge, Noir..."
                          disabled={isLoading}
                        />
                      </div>
                      <div className="space-y-1">
                        <Label>Taille</Label>
                        <Input
                          value={v.size}
                          onChange={(e) =>
                            updateVariant(v.id, "size", e.target.value)
                          }
                          placeholder="S, M, L, 40..."
                          disabled={isLoading}
                        />
                      </div>
                      <div className="space-y-1">
                        <Label>Prix</Label>
                        <Input
                          type="number"
                          step="0.01"
                          value={v.price}
                          onChange={(e) =>
                            updateVariant(v.id, "price", e.target.value)
                          }
                          disabled={isLoading}
                        />
                      </div>
                      <div className="space-y-1">
                        <Label>Stock</Label>
                        <Input
                          type="number"
                          value={v.quantity}
                          onChange={(e) =>
                            updateVariant(v.id, "quantity", e.target.value)
                          }
                          disabled={isLoading}
                        />
                      </div>
                      <div className="flex justify-end">
                        <Button
                          type="button"
                          variant="outline"
                          size="sm"
                          onClick={() => removeVariant(v.id)}
                          disabled={isLoading}
                        >
                          Supprimer
                        </Button>
                      </div>
                    </div>
                  ))}

                  <Button
                    type="button"
                    variant="outline"
                    size="sm"
                    onClick={addVariant}
                    disabled={isLoading}
                  >
                    + Ajouter une variante
                  </Button>
                </div>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="description">Description</Label>
              <textarea
                id="description"
                name="description"
                className="border rounded-md px-3 py-2 w-full min-h-[120px]"
                value={formData.description}
                onChange={handleChange}
                disabled={isLoading}
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="status">Statut</Label>
              <select
                id="status"
                name="status"
                className="border rounded-md px-3 py-2 w-full"
                value={formData.status}
                onChange={handleChange}
                disabled={isLoading}
              >
                <option value="published">Publi√©</option>
                <option value="draft">Brouillon</option>
              </select>
            </div>

            <div className="flex justify-end gap-3">
              <Button
                type="button"
                variant="outline"
                onClick={() => router.back()}
                disabled={isLoading}
              >
                Annuler
              </Button>
              <Button type="submit" disabled={isLoading}>
                {isLoading ? "Enregistrement..." : "Cr√©er le produit"}
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}
